body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-container{
  background-color: white;
  width: 100%;
  top: 0;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: "Arial Black", "Arial Bold", Gadget, sans-serif;
}

.menu-container h1{
  margin-left: 20px;
}

.menu-container img{
    height: 30px;
    padding-right: 35px;
    /* padding-top: 30px; */
}

.menu-container a{
  text-decoration: none;
  color: black;
}

.menu-container a:hover{
  text-decoration: underline;
}

.container{
  margin-top: 70px;
}

.items-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* justify-content: space-between; */
  margin: 10px;
}

.items{
  width: 10%;
  flex-shrink: 0; 
  flex-basis: calc(25% - 20px);
  padding: 10px;
}

.items img{
  width: 100%;
}

.admin-container, .cartPage-container{
  margin-top: 120px;
}

.indiv-inventory-form img{
  width: 20%;
}

.indiv-inventory-form input{
  margin: 30px;
  width: 200px;
}